.sub-cont{
  padding-top:7rem
}
.sub-inner{
  padding-top: 9.4rem;
}
.sub-title-wrap{
  text-align: center;
}
.sub-title-wrap h2{
  font-size: 4rem;
  font-weight: 500;
}
.sub-title-wrap p{
  font-size: 1.8rem;
  font-weight: 500;
  color: #939393;
  margin-top: 1rem;
}
.card-wrapper{
  padding: 9rem 4rem 0;
}
.card-inner{
  display: flex;
  flex-wrap: wrap;
}
.card-box{
  width: calc(25% - 3.4rem);
  margin-right: 4.5rem;
  margin-bottom: 6rem;
}
.card-box:nth-child(4n){
  margin-right: 0;
}
.card-img{
  position: relative;
  padding-bottom: 126%;
  overflow: hidden;
}
.card-img::after{
  content: '';
  position: absolute;
  top: 2.4rem;
  right: 2.4rem;
  width: 3.2rem;
  height: 3.2rem;
  background: url(../../assets/images/sub/choice.svg) no-repeat center;
}
.card-img img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.card-infor{
  position: relative;
  margin-top: 2rem;
}
.card-title{
  font-size: 1.8rem;
  color: #333;
  font-weight: 500;
}
.card-txt{
  margin-top: 0.5rem;
  font-size: 1.4rem;
  color: #939393;
}
.card-price{
  font-size: 1.7rem;
  color: #111;
  font-weight: 700;
  display: flex;
  align-items: center;
  line-height: 1;
  margin-top: 0.5em;
}
.card-price span{
  font-size: 1.6rem;
  color: #AC0C1B;
  margin-right: 0.5rem;
}
.card-price i{
  font-size: 1.3rem;
  font-weight: 500;
  color: #a9a9a9;
  text-decoration: line-through;
  margin-left: 0.5rem;
}
.card-star{
  display: flex;
  align-items: center;
  margin-top: 0.5rem;
}
.card-star i{
  margin-right: 0.3rem;
}
.card-star .score{
  font-size: 1.3rem;
  color: #939393;
  margin-left: 0.2rem;
}
.card-star .badge-gift{
  background-color: #A9A9A9;
  font-size: 1.2rem;
  font-weight: 500;
  color: #fff;
  padding: 0.1rem 0.6rem;
  border-radius: 3px;
  margin-left: 0.3rem;
}
.card-star .badge-cupon{
  background-color: #787878;
  font-size: 1.2rem;
  font-weight: 500;
  color: #fff;
  padding: 0.1rem 0.6rem;
  border-radius: 3px;
  margin-left: 0.3rem;
}
.cart-btn{
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  z-index: 3;
}