@import url(./fonts.css);
:root {
  font-size: 10px;
}
* {
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none;
  -moz-text-size-adjust: none;
  -ms-text-size-adjust: none;
  text-size-adjust: none;
  box-sizing: border-box;
}
body {  
  width:100%;
  font-family: 'Pretendard', 'Arial', sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
} 
html {
  width: 100%;
  height: 100%;
}
html,
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
p,
address,
pre,
cite,
form,
fieldset,
input,
textarea,
select,
table,
th,
td {
  margin: 0;
  padding: 0;
}
body::-webkit-scrollbar {
  width: 0.9rem; /* 스크롤바의 너비 */  
}
body::-webkit-scrollbar-thumb {
  height: 30%; /* 스크롤바의 길이 */
  background: rgba(126, 131, 156, 0.65);
  border: 0.1rem solid rgba(83, 83, 83, 0.15); /* 스크롤바의 색상 */
  border-radius: 1rem;
}
body::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.15); /*스크롤바 뒷 배경 색상*/
}
body.scrolloff {
  overflow: hidden;
}

table {
  border-collapse: separate;
  border-spacing: 0;
  border: 0 none;
}
address,
caption,
i,
em,
cite {
  font-weight: normal;
  font-style: normal;
}
ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: #333;
}
img {
  max-width: 100%;
}
.blind {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  font-size: 0;
  line-height: 0;
  text-indent: -9999px;
}
input,
select,
button {
  border: 0 none;
  outline: none;
  margin: 0;
}
button{
  background-color:transparent
}
caption,
th,
td {
  text-align: left;
  font-weight: normal;
  border: 0;
}


select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
button,
input[type='text'],
input[type='button'] {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0;
}

/*input*/
.input-control {
  display: block;
  width: 100%;
  height: calc(4rem + 0.5rem);
  padding: 1rem 2rem 1rem 1.5rem;
  background-color: #fff;
  background-clip: padding-box;
  border: 0.1rem solid #dedede;
  border-radius: 0.4rem !important;
  box-shadow: inset 0 0 0 transparent;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  font-size: 1.6rem;
  font-weight: 400;
  color: #000;
}

/*select*/
.sort-form-select {
  display: block;
  width: 100%;
  height: calc(4rem + 0.5rem);
  background: #fff url('../images/common/select_ico.svg') calc(100% - 2rem)
    center no-repeat;
  background-size: 1.3rem 1.4rem;
  padding: 0.375rem 2.6rem 0.375rem 1.5rem;
  border: 0.1rem solid #dedede;
  border-radius: 0.3rem;
  box-shadow: inset 0 0 0 transparent;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  font-size: 1.6rem;
  font-weight: 400;
  color: #000;
}

/*badge*/
.badge {
  display: inline-block;
  padding: 0.3rem 0.6rem;
  border-radius: 0.4rem;
  text-align: center;
  cursor: pointer;
  font-weight: 400;
  font-size: 1.4rem;
  color: #fff;
}
/*btn-frame*/
.btn-frame {
  display: inline-block;
  height:4rem;
  line-height:4rem;
  padding: 0 1.975rem; 
  text-align: center;
  cursor: pointer; 
  font-family: 'Montserrat';
  font-weight: 500;
  font-size: 1.4rem;
  transition:all 0.25s ease-in-out
}
.bg-White{
  background-color:#fff
}
.bg-green{
  background-color:#1a8034
}

.bg-black{
  color:#000
}
.black-line{
  border: 1px solid rgba(0 0 0 / 0.4); 
  color:#111
}
.white-line{
  border: 1px solid rgba(255 255 255 / 0.4);
  color:#fff
}
.btn-frame:hover.white-line{
  border: 1px solid rgba(255 255 255 / 0.4);
  background-color: #fff;
  color:#111
}
.btn-frame:hover.black-line{
  border: 1px solid rgba(0 0 0 / 0.4); 
  background-color: #111;
  color:#fff
}





@media (max-width: 640px) {  
  .btn-frame {
    height: 3.9rem;
    line-height: 3.9rem;
    font-size: 1.4rem;
  }
}

@media (max-width: 375px) {
  .badge {
    padding: 0.04rem 0.7rem 0.05rem;
    line-height: 2.1rem;
    font-size: 1.2rem;
  }
}
