:root{
    --style-default-image: url('../../../assets/images/main/style-view_bg01.png');
    --style-image-01: url('../../../assets/images/main/style-view_bg01.png');
    --style-image-02: url('../../../assets/images/main/style-view_bg02.png');
    --style-image-03: url('../../../assets/images/main/style-view_bg03.png');
}

.sec07-inner{
    position: relative;
    padding-bottom: 43%;
    opacity: 0;
}
.sec07-box{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: var(--style-default-image);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-image 0.5s ease-in-out;
}
.view-box{
    flex: 1;
    width: 100%;
    height: 100%;
    display: flex; 
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(17, 17, 17, 0.4);
    backdrop-filter: blur(2px);
    border-right: 1px solid rgba(217, 217, 217, 0.3);
    transition: all .35s ease;
}
.view-title{
    font-size: 3rem;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.3);
    transition: color .35s ease;
}
.view-box .buy{
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: rgba(255, 255, 255, 0.3);
}
.view-box:hover{
    background-color: transparent;
    backdrop-filter:none;
}
.view-box:hover .view-title{
    color: #fff;
}
.view-box:hover .buy{
    border: 1px solid #fff;
    color: #fff;
}
.view-box .buy:hover{
    color: #111;
    background-color: #fff;
}
.sec07-box[data-img='bg-image_01'] {
    background-image: var(--style-image-01);
}
.sec07-box[data-img='bg-image_02'] {
    background-image: var(--style-image-02);
}
.sec07-box[data-img='bg-image_03'] {
    background-image: var(--style-image-03);
}