.headr-wrap{
  position:relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 7rem;
  padding-left:4rem;
  padding-right:4rem;
}
.headr-wrap h1{
  position:absolute;  
  left:4rem;
  top:50%;
  transform:translateY(-50%)
}
.headr-wrap h1 a{
  display:block;
  width:7rem;
  height: 3.3rem;
  background: url('../../assets/images/common/nike-logo.png') center no-repeat;
  background-size: 100%;
}
header nav ul{
  display:flex;
  align-items: center; 
}
header nav ul > li > a{
  position: relative;
  display: block;
  margin:0 0.9rem;
  padding: 2.4rem 0.5rem 2.5rem;
  text-transform:uppercase;
  font-family: 'Montserrat';
  font-size:1.4rem;
  font-weight:600;
  color:#fff
}
.nav-list li:focus::after {
  display:none
}
header nav ul > li > a::after{
  content: '';
  position: absolute;
  width:0;
  height: 0.1rem;
  bottom:0rem;
  left:50%;
  background-color: #000;
  opacity: 0;
  z-index: 1;
  transition: all 0.35s;
}
header nav ul > li.active:hover > a::after{
  width: 100%;
  opacity: 1;
  transform: translateX(-50%);
}
.menu-r{
  display:flex;
  align-items: center;
  position:absolute;
  right:4rem;
  top:50%;
  transform:translateY(-50%);
  z-index: 200;
}
.btn-my{
  position:relative;
  display: inline-block;
  width:2.4rem;
  height: 2.4rem;
  margin-left:2.4rem;
  background:url('../../assets/images/common/icon-profile.svg') center no-repeat;
  cursor:pointer
}
.btn-my img{
  width:2.4rem
}
.btn-search{ 
  display: inline-block;
  width:2.4rem;
  height: 2.4rem;
  margin-left:2.7rem;
  background:url('../../assets/images/common/icon-search.svg') center no-repeat;
  cursor:pointer
}
.btn-search img{
  width:2.4rem
}
.my-memo{  
  position:absolute;
  top: -0.6rem;
  right: -1.3rem;
  width:1.8rem;
  height:1.83rem;
  background:#fff;
  border-radius:50%;
  line-height:1.8rem;
  text-align: center;
  font-size:1.3rem;
  color:#111;
}

/*GNB CSS*/
.nav-list02{
  display: flex;
  align-items: center;
  height: 100%;
}
.depth2-wrap{
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%; 
  background-color:#fff; 
  max-height: 80rem; 
  display: flex;
  justify-content: center; 
  padding:5rem 0 4rem;
  border-top: 1px solid #E9E9E9;
  border-bottom: 1px solid #E9E9E9;
  transform: scaleY(0); 
  opacity: 0; 
  visibility: hidden; 
  z-index: 1;
  transform-origin: top center; 
  transition: transform 1ms linear, opacity 0ms linear .35s, visibility 0ms linear .35s;
}
.nav-list.is-open > ul > li.active .depth2-wrap{
  transform: scaleY(1); 
  opacity: 1; 
  visibility: visible; 
  transition: transform .2s ease, opacity 0ms ,visibility 0ms;
}
.nav-list.is-open > ul > li .depth2-wrap{ 
  transform: scaleY(1); 
  transition: none;
}
.depth2-list{
  display:inline-block;  
  margin-right:12rem;
  opacity: 0; 
  transform: translateY(-2rem);
  z-index: 2; 
}
.nav-list.is-open > ul > li.active .depth2-list{
  opacity: 1; 
  transform: translateY(0);
  transition:all 0.6s ease-in-out
}
.depth2-list:last-child{
  margin-right:0;
}
.depth2-list > dt{
margin-bottom:2.5rem;
font-family: 'Poppins';
font-size: 1.6rem;
font-weight: 600;
line-height: normal;
letter-spacing: -0.4px;
text-transform: capitalize;
color:#111
}
.depth2-list > dd{
  text-align: left;
}
.depth2-list > dd > a{
  display: block; 
  margin-bottom:1rem;
  font-size: 1.4rem;
  text-transform: capitalize;
  color: rgba(102, 102, 102, 1);
}
.depth2-list > dd:hover > a{
  color:#111;
  font-weight: 700;
}
.dim-header{
  position: fixed;
  top:0;
  left:0;
  width:100%;
  height: 100vh;
  background-color: rgba(0 0 0 /0.45);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: all .35s;
  z-index: 10;
}
body.gnb-on .dim-header{
  visibility: visible;
  opacity: 1;
  backdrop-filter:blur(.13rem);
}
.h-dd{
  height: 10rem;
}

