.visual-box{
  position: relative;
  height:108rem;
}
.visual-img{
  position: absolute;
  top:0;
  left:0;
  width:100%;
  height: 100%;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}
.visual-text-box{
  position: relative;
  /* max-width:148.2rem;
  margin:0 auto; */
  padding-top:21.5rem;
  /* padding-left:21.9rem; */
  padding-left:12%;
  line-height: 11.6rem;
  font-family: 'Michroma';
  font-size: 12rem;
  font-weight: 400; 
  color:#fff
}
.visual-text-box .first-text,
.visual-text-box .second-text,
.visual-text-box .third-text{
  overflow: hidden;
}
.visual-text-box .third-text{
  max-width:139rem
}
.visual-text-box .first-text span,
.visual-text-box .second-text span,
.visual-text-box .third-text span{
  display: inline-block;
}
.visual-text-box .second-text span,
.visual-text-box .third-text span{
  transform-origin: center;
}
.visual-text-box .first-text span,
.visual-text-box .second-text span,
.visual-text-box .third-text span
{
  opacity:0;
}
.visual-text-box .second-text span:nth-child(2){
  transform: translateX(-100%);
}
.visual-text-box .third-text span{
  transform-origin: center;
}
.visual-swiper-box{
  position: relative;  
  overflow: hidden;
  opacity:0;
}
.visual-swiper{
  position: relative;
  display:flex; 
  height: 27rem;
  padding-left:12%;
  /* padding-left:21.9rem; */
  /* max-width: 148.2rem;
  margin:0 auto;  */  
  margin-top:7.7rem
}
.visual-swiper:before{
  content:'';
  position: absolute;
  left:12%;
  width:100%;
  height: 100%;
  background-color: rgba(0 0 0 / 0.4);
}

.swiper-left{
  position: relative;
  flex:none;
  /* width:62.1rem; */
  width:36.75%;
  padding:7.1rem 0 6.2rem 7rem;
  z-index: 1;
  font-family: 'Pretendard';
  font-size: 1.4rem;
  font-weight: 400;
  color:rgba(255 255 255 / 0.4)
}
.swiper-left h3{
  margin-bottom:1rem;
  font-family: 'Michroma';
  font-size: 2.3rem;
  font-weight: 400;
  color:#fff
}
.banner-copy{
  margin-bottom:2.5rem;
  color:rgba(255 255 255 / 0.4)
}
.swiper-rigt{
  width:63.25%;
  height: 100%;
  /* padding-left:62.1rem; */
  overflow: hidden;
  background-color: rgba(0 0 0 / 0.4);
}
.main-banner-swiper{
  position: relative;
  height: 100%;
}
.main-banner-swiper .swiper{
  position: relative;
  height: 100%;
  overflow: visible;
}
.main-banner-swiper .swiper figure{
  display: block;
  position: relative;
  height: 27rem;
}
.main-banner-swiper .swiper figure a{
  position: absolute;
  top:0;
  left:0;
  width: 100%;;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.swiper-btnbox{
  display: flex;
  position: relative;
  z-index: 1;
}
.slider_prev, .slider_next { 
  display:block;
  width:3.8rem;
  height: 3.8rem;
  cursor: pointer;
  text-align: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
}
.slider_prev{
  margin-right:1rem;
  background-image: url('../../assets/images/common/icon-slider_prev.png');
}
.slider_next{
  background-image: url('../../assets/images/common/icon-slider_next.png');
}
.slider_prev.swiper-button-disabled, .slider_next.swiper-button-disabled{
  opacity:0.3
}
.section1 h2{
  position: relative;
  margin-bottom:1rem;
  padding-top:2rem;
  font-family: 'Montserrat';
  font-size: 2.8rem;
  font-style: normal;
  font-weight: 600; 
  letter-spacing: 1.96rem;
  text-transform: uppercase;
}
.section1 h2:before{
  content: '';
  position: absolute;
  top:0;
  left:0;
  width:2rem;
  height:0.4rem;
  background-color: #000;
}
.section1 p{ 
  margin-bottom:7.4rem;
  font-size: 1.3rem;
  font-weight: 400;
  color: #111;
}
