footer{
  padding:4.2rem 10rem 3.5rem;
  background: #111;
  font-size:1.5rem;
  color:#aaa
}
.footer-inner{
  display: flex;
  align-items: center; 
}
.foot-logo{
  display:block;
  width:7rem;
  height: 3.3rem;
  background: url('../../assets/images/common/nike-logo.png') center no-repeat;
  background-size: 100%;
}
.co-info{  
  flex:1;
  margin-left:7.3rem;  
}
.link-info{
  display: flex;
  align-items: center;
  margin-bottom:1rem
}
.link-info a{ 
  color: rgba(119, 119, 119, 0.76);
}
.link-info li{
  margin:0 0.7rem
}
.link-info li:nth-child(1){
  margin-left:0;
}
.link-info li:nth-child(2) a{
  color:#fff
}
.link-info li:last-child{
  margin-right:0;
}
.sns-link{
  display:flex;
  align-items: center;
  margin-left:auto;
}
.sns-link a{
  margin:0.6rem
}
.sns-link a:first-child{
  margin-left:0
}
.sns-link a:last-child{
  margin-right:0
}
.copyright{
  color: rgba(119, 119, 119, 0.76);
}
address{
  margin-bottom:0.2rem;
  color: rgba(119, 119, 119, 0.76);
}
address span{
  margin-right:0.8rem
}
address span i{
  margin: 0 0.5rem
}