.shop-swiper{
  opacity:0
}
.shop-swiper a{
  display:block;
  height: 100%; 
}
.shop-img{
  position: relative;
  height: 42.9rem;
}
.shop-img .img-box{
  position: absolute;
  top:0;
  left:0;
  width:100%;
  height: 100%;
}
.shop-info{
  position: relative;
  display: flex;
  flex-direction: column; 
  padding:2rem; 
  font-family: 'Inter';
  z-index: 1;
}
.shop-info > strong{ 
  margin-bottom:1rem; 
  font-size: 2.2rem;
  font-weight: 600;
  color:#111
}
.shop-info > i{ 
  margin-bottom:2rem;
  font-size: 1.8rem;
  font-weight: 500;
  color:#111
}
.shop-swiper .pagnation-box{
  position: relative;
  height: 0.3rem!important;
  margin: 2.5rem  0rem 0;
  background-color: rgba(0 0 0 / 0.05);
}
.shop-swiper .shop-swiper-pagination-swiper-pagination .swiper-pagination-progressbarl{
  height: 0.3rem;
}
.shop-swiper .shop-swiper-pagination .swiper-pagination-progressbar-fill{
  height: 0.3rem;
  background-color: #000;
}