.sec06-grid-wrap{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 2rem;
    opacity: 0;
}
.gird-item01{
    position: relative;
    padding-bottom: 81.4%;
    grid-column: 1/3;
    grid-row: 1/3;
}
.grid-item02{
    position: relative;
    padding-bottom: 80.9%;
    grid-column: 3/4;
    grid-row: 1/2;
}
.grid-item03{
    position: relative;
    padding-bottom: 80.9%;
    grid-column: 4/5;
    grid-row: 1/2;
}
.grid-item04{
    position: relative;
    padding-bottom: 80.9%;
    grid-column: 3/4;
    grid-row: 2/3;
}
.grid-item05{
    position: relative;
    padding-bottom: 80.9%;
    grid-column: 4/5;
    grid-row: 2/3;
}
.gird-item-bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
    display: flex;
    flex-direction: column;
    transition: background .35s ease;
}
.gird-item-bg:hover{
    background-size: 110%;
}
.gird-big{
    padding: 6rem;
}
.gird-small{
    padding: 3rem;
}
.grid-infor{
    text-align: right; 
    margin-top: auto;
}
.grid-name{
    font-size: 2.4rem;
    font-weight: 500;
    color: #fff;
}
.gird-small .grid-name{
    font-size: 2rem;
}
.buy-btn-wrap{
    margin-top: 2rem;
}
.buy{
    position: relative;
    border: 1px solid #fff;
    font-size: 1.4rem;
    color: #fff;
    padding: 1rem 2rem;
    cursor: pointer;
    transition: all .35s;
}
.buy:hover{
    color: #111;
    background-color: #fff;
}