.location-wrap{
    width: 100%;
    height: 50px;
    border-top: 1px solid #e9e9e9;
    border-bottom: 1px solid #e9e9e9
}
.location-inner{
    max-width: 75.4rem;
    margin: 0 auto;
    height: 100%;
}
.location-list{
    display: flex;
    align-items: center;   
    height: 100%;
}
.location-list > li{
    position: relative;
    flex: 1;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-right: 1px solid #e9e9e9;
    transition: background-color .1s ease;
}
.location-list > li:first-child{
    border-left: 1px solid #e9e9e9;
}
.location-list > li > span{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    font-size: 1.4rem;
    color: #656565;
    font-weight: 400;
    cursor: pointer;
    transition: color .1s ease;
}
.location-list > li > span:after{
    content: '';
    width: 0.4rem;
    height: 0.4rem;
    border: 2px solid #656565;
    border-top: none;
    border-left: none;
    transform: translateY(-0.2rem) rotate(45deg);
    margin-left: 1rem;
    transition: border-color .1s ease;
}
.location-depth2-list{
    position: absolute;
    top: calc(100% + 0.1rem);
    left: 50%;
    transform: translateX(-50%) scaleY(0);
    transform-origin: top center;
    
    background-color: #fff;
    width: 100%;
    padding: 3rem;
    border: 1px solid #e9e9e9;
    border-top: none;
    z-index: 99;
}
.location-depth2-list > li > a{
    display: block;
    font-size: 1.4rem;
    color: #7e7e7e;
    padding-bottom: 1.2rem;
    transition: color .25s;
}
.location-depth2-list > li:last-child > a{
    padding-bottom: 0;
}

.location-list > li.active{
    background-color: #F7F7F7;
}
.location-list > li.active > span{
    color: #111;
    font-weight: 600;
}
.location-list > li.active > span:after{
    border-color: #111;
}

.location-list > li.active .location-depth2-list{
    transform: translateX(-50%) scaleY(1);
    background-color: #F7F7F7;
    transition: transform .1s ease-in;
}
.location-depth2-list > li > a:hover{
    color: #111;
    font-weight: 600;
}