.packge-ani{   
    opacity: 0;
    transform-origin: 'top'; 
}
.title-wrap{
    opacity:0;
    transform:translateY(20rem)
}
.flex-wrapper{
    display: flex;    
}
.flex-wrapper:last-child{
    margin-top: 2rem;
}
.flex-box.item01{
    display: flex;
    flex-direction: column;
    width: 39.7%;
    margin-right: 2rem;
    height: auto;
}
.flex-box-inner{
    position: relative;
    width: calc(100% - 39.4%);
    padding-bottom: 51.2%;
}
.flex-box.item02{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 7rem;
}
.flex-box03-inner{
    width: 40.5%;
    position: relative;
    margin-right: 2rem;
    padding-bottom: 51.2%;
}
.flex-box.item03{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 7rem;
}
.air-max-item {
    position: relative;
    background-color: #F8F8F8;
    padding: 7rem;
    margin-top: auto;
}
.air-max-img{
    position: relative;
    overflow: hidden;
    margin-top: -20.4rem;
    left: 50%;
    transform: translateX(-50%);
    max-width: 45rem;
    text-align: center;
    padding-bottom: 6.8rem;
}
.air-max-img img{
    width: 100%;
}
.item01-infor-wrap{
    text-align: right;
}
.item01-infor-wrap h3{
    font-size: 4.6rem;
    color: #111;
    font-weight: 600;
}
.item01-infor-wrap p{
    font-size: 1.3rem;
    color: #111;
    margin-top: 1rem;
}
.item02-infor-wrap{
    margin-top: auto;
    text-align: right;
}
.item02-infor-wrap h3{
    font-size: 4.6rem;
    color: #fff;
    font-weight: 600;
}
.item02-infor-wrap p{
    font-size: 1.3rem;
    color: #fff;
    margin-top: 1rem;
}
.nike-intro{
    width: 41.3%;
    margin-right: 2rem;
    height: auto;
    margin-top: auto;
    color: #666;
    font-size: 1.5rem;
    font-weight: 400;
}
.air-pos-item{
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 7rem 7rem 7rem 5.4rem;
    background-color: #F6F6F6;
    max-height: 48.1rem;
    width: 21%;
}
.air-pos-img{
    width: 100%;
    position: absolute;
    top: 0; 
    left: 50%;
    transform: translateX(-50%);
    padding: 39%;
    overflow: hidden;

}
.air-pos-img img{
    position: absolute;
    top: -3rem;
    left: 0;
    width: 100%;
}
.item04-infor-wrap{
    margin-top: auto;
    text-align: right;
}
.item04-infor-wrap h3{
    font-size: 4.6rem;
    color: #111;
    font-weight: 600;
}
.item04-infor-wrap p{
    font-size: 1.3rem;
    font-weight: 400;
    margin-top: 1rem;
}