.scrollof{
    overflow: hidden;
}
.scrollof header.active{
    transform: translateY(0);
}
.scrollof header.on .all-menu i{
    background:#fff;
}
.header_inner.active .all-menu i{
    background:#000;
}

header.on .all-menu i{
    background:#000;
}

.all-menu{
    position:relative;
    display:block;
    width:2rem;
    height:1.6rem;   
    margin-left:3.5rem;
    background-color:transparent;
    cursor:pointer;
    /* z-index: 2000; */
}
.all-menu i{
    position:absolute;   
    left:0;
    width:100%;
    height:0.2rem;
    margin:auto;
    background:#fff;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
.all-menu i:nth-of-type(1){   
    top:0; 
}
.all-menu i:nth-of-type(2){   
    top:0.7rem;     
}
.all-menu i:nth-of-type(3){   
    bottom:0;     
}
.all-menu.active i:nth-of-type(1){
    transform-origin: center;
    -webkit-transform: rotate(-135deg);
    -ms-transform: rotate(-135deg);
    transform: rotate(-135deg);
    top: 0.7rem;  
    -webkit-transition: 0.45s;
    transition: 0.45s;
}
.all-menu.active i:nth-of-type(2){
    opacity:0;
    transform: translateX(100%);
    -webkit-transition: 0.45s;
    transition: 0.4s;
}
.all-menu.active i:nth-of-type(3){
    transform-origin: center;
    -webkit-transform: rotate(135deg);
    -ms-transform: rotate(135deg);
    transform: rotate(135deg);
    top:0;  
    -webkit-transition: 0.45s;
    transition: 0.45s;
}
.all-nav {
    position: relative;
    height: auto;
    /* display: none; */
    /* left: 0;
    right: 0;   */
    /* display: none; */
    /* max-width: 1920px;
    min-width: 1440px;   
    margin: 0 auto;
    background: #fff;   */
}
/* .all-nav.on{
    display:block
} */
.all-nav-wrap{
    position: fixed;
    height: 0;
    top:0;
    left: 0;
    width: 100%;   
    padding-bottom:2rem;   
    /* display:flex;
    justify-content:center; */
    background: #2A2A2A;
    /* background: rgb(42 42 42 / 85%); */
    visibility: hidden;
    pointer-events: none;
    -webkit-transition: all 0.45s ease-in-out;
    transition: all 0.45s ease-in-out;
    z-index: 150;
}
.all-nav-wrap .menu_list::-webkit-scrollbar {
  width: 0.4rem; /* 스크롤바의 너비 */  
}
.all-nav-wrap .menu_list::-webkit-scrollbar-thumb {
    height: 20%; /* 스크롤바의 길이 */
    background: rgba(143, 143, 143, 1);    
    border-radius: 1.6rem;
}
.all-nav-wrap .menu_list::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0); /*스크롤바 뒷 배경 색상*/
}
.all-nav-wrap.on{
    display: block;
    height: 100vh;
    visibility: visible;
    pointer-events: visible;      
}
.all-nav-wrap .inner {
    padding: 0 0 74px 309px;
}
.all-nav-wrap .menu_list{
    margin-top: 7rem;
    margin-left:4rem;
    margin-right:4rem;
    max-height: calc(100vh - 9rem);
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    transform-origin: top center;
    transform: translateY(-80%);
    opacity:0;     
}
.all-nav-wrap.on .menu_list{
    opacity:1;
    transform: translateY(0);
    transition: all 1.5s;
}
.all-nav-wrap .menu_list section {   
    margin-top: 5rem;
    color:#fff
}
.all-nav-wrap .menu_list section:first-child{
    margin-top:0
}
.all-nav-wrap .menu_list .almenu-title{   
    position:relative;
    margin-bottom:2.8rem;
    padding-bottom:2.5rem;
    font-family: 'Montserrat';
    font-size: 4rem;
    font-style: normal;
    font-weight: 600;  
    letter-spacing: -1px;
    text-transform: uppercase;
    color: #FFF;
}
.all-nav-wrap .menu_list .almenu-title:after{
    content:'';
    position: absolute;
    bottom:0;
    left:0;
    width:100%;
    height:0.1rem;
    background-color:#525252
}

.all-nav-wrap .menu_list section ul {
    display: inline-block;   
    margin-right:12rem;
    vertical-align: top;
}
.all-nav-wrap .menu_list section ul:last-child{
    margin-right:0;
}
.all-nav-wrap .menu_list section ul > li > .d1 {
    display: inline-block;
    margin-bottom:1.2rem;
    font-family: 'Poppins';
    font-size: 2rem;
    font-weight: 600;   
    text-transform: capitalize;
    color: #fff;   
}
.all-nav-wrap .menu_list section > ul > li > .d2 {
    display: inline-block;
    margin-bottom:1.1rem;
    margin-left:0;
    padding:0;
    font-family: 'Poppins';
    font-weight: 600;
    font-size: 1.5rem;
    color: #ABABAB;   
}
.all-nav-wrap .menu_list section > ul > li > a.d1:hover, .all-nav-wrap .menu_list section > ul > li > a.d2:hover {
    color: #fff;   
}
.all-nav-wrap .banner {
    display: inline-block;
    vertical-align: top;
    margin-top: 7.2rem;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
.all-nav-wrap .banner a {
    display:block;
    font-size: 0;
    border-radius: 1.6rem;
    overflow: hidden;
    -webkit-box-shadow: 0 1.5rem 4.5rem 0 rgba(0, 0, 0, 0.2);
    box-shadow:  0 1.5rem 4.5rem 0 rgba(0, 0, 0, 0.2);
}
.dim {
    position: fixed;
    left: 0;
    top:0rem;
    width:calc(100% - 0rem);   
    height:100vh;
    /* display: none; */
    background: rgb(42 42 42 / 45%);
    z-index: 100;   
    pointer-events: none;
    opacity:0;    
    pointer-events: none;
    transition: all 0.5s ease-in-out;
}
.dim.on{  
    /* display: block; */
    opacity:1;
    backdrop-filter: blur(0.75rem);
}

@media (max-width:1024px){
    .all-menu{
        display: none;
    }
    .all-nav{
        display: none;
    }
}
