.sec02-flex-wrap{
    display: flex;
    align-items: center;
    overflow: hidden;
}
.new-style-inner{
    position: relative;
    flex: 1;
    overflow: hidden;
    padding-bottom: 31.7%;
}
.new-style-inner:first-child{
    margin-right: 2rem;
    opacity:0;
    transform: translateY(700);
    transform-origin: top;
}
.new-style-inner:nth-child(2){
    opacity:0;
    transform: translateY(700);
    transform-origin: bottom;
}
.new-style-bx{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex; 
    flex-direction: column;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 6rem 9rem;
}

.new-style-text-wrap{
    margin-top: auto;
    text-align: right;
}
.new-style-title{
    font-size: 4rem;
    color: #fff;
    font-weight: 700;
    text-transform: uppercase;
}
.new-style-sub01{
    color: #fff;
    font-size: 1.8rem;
    font-weight: 500;
    margin-top: 1.5rem;
}
.new-style-sub02{
    font-size: 1.4rem;
    font-weight: 300;
    color: #fff;
    margin-top: 1rem;
}
.detail_btn-wrap{
    margin-top: 3rem;
}



