.best-inner{
  max-width:189rem;
  margin: 0 auto 0;
  opacity: 0;
}
.best-swiper{
  position: relative;
  overflow: hidden;  
}
.best-swiper .swiper{
  /* overflow-y: visible; */
}
.pagnation-box{
  padding:0 3.9%
}
.best-swiper .best-swiper-pagination{
  position: relative;
  /* width:calc(100% - 16rem)!important; */
  height: 0.3rem!important;
  margin: 2.5rem  0rem 0;
  background-color: rgba(0 0 0 / 0.05);
}
.best-swiper .best-swiper-pagination .swiper-pagination-progressbar.swiper-pagination-horizontal{
  height: 0.3rem;
}
.best-swiper .best-swiper-pagination .swiper-pagination-progressbar-fill{
  background-color: #000;
}
.best-swiper .swiper-slide{
  display: flex;
  flex-direction: column;
  height: 81rem;
}
.best-swiper .swiper-backface-hidden .swiper-slide{
  transform-origin: 50% 88%;
  transform: scale(.78) translateZ(0);
  transition: all 0.35s;
}
.best-swiper .swiper-slide.swiper-slide-active{
  transform: scale(1) translateZ(0) !important;
}
.best-swiper .swiper-slide a{
  display:block;
  height: 100%; 
}
.best-product{
  display: flex;
  flex-direction: column;
  height: 100%;
}
.best-img{
  position: relative;
  flex:1;
  height: 49.6rem;  
}
.best-swiper .swiper-slide.swiper-slide-active .best-img{
  background: rgb(128,128,128);
  background: linear-gradient(153deg, rgba(128,128,128,0.85) 0%, rgba(117,117,117,0.75) 14%, rgba(255,255,255,1) 100%);
}
.best-swiper .swiper-slide.swiper-slide-active .img-box{
  height:101.75%;
  background-color: transparent;
  background-position:center 1.5rem ;
}
.img-box{
  position: absolute;
  top:-1.3rem;
  left:0;
  width: 100%;;
  height: 100%;
  background-position:center ;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #f1f1f1;
}
.product-info{
  margin-top:auto;
  padding-top:3.4rem;
  padding-bottom:2.5rem;  
  font-size: 1.8rem;  
  font-weight: 600;
}
/* .best-swiper .swiper-slide .product-info{
  font-size: 2.15rem;
} */
.best-swiper .swiper-slide.swiper-slide-active .product-info{
  font-size: 1.8rem;  
}
.best-swiper .swiper-slide.swiper-slide-active .best-img{
  /* height: 70rem; */
}
.product-info strong{
  margin-bottom:1rem
}
.product-info p{
  margin-bottom:1rem;
  color:#939393
}
.product-info .product-price{  
  font-weight: 700;
  color:#111
}
.product-info .product-price span{
  margin-right:0.8rem
}
.product-info .product-price span:nth-of-type(3){
  margin-right:0;
  font: 1.7rem;
  font-weight: 500;
  text-decoration: line-through;
  color:#A9A9A9
}
.product-info .product-price span:nth-of-type(1){
  color:#AC0C1B
}
