:root{
    --default-image: url('../../../assets/images/main/member-gift_bg01.png');
    --image-01: url('../../../assets/images/main/member-gift_bg01.png');
    --image-02: url('../../../assets/images/main/member-gift_bg02.png');
    --image-03: url('../../../assets/images/main/member-gift_bg03.png');
}
.sec03-inner{
    position: relative;
    width: 100%;
    padding-bottom: 43.5%;    
}
.sec03-box{
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: var(--default-image);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    transition: background-image 0.5s ease-in-out;
}
.sec03-box[data-img='bg-image_01'] {
    background-image: var(--image-01);
}
.sec03-box[data-img='bg-image_02'] {
    background-image: var(--image-02);
}
.sec03-box[data-img='bg-image_03'] {
    background-image: var(--image-03);
}
.member-box{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-right: 1px solid rgba(255, 255, 255, 0.2);
    background: transparent;
    transition: background .35s;
}
.member-title{
    font-size: 6rem;
    font-weight: 700;
    color: rgba(255, 255, 255, 0.2);
    transition: color .35s;
}
.member-txt{
    font-size: 1.6rem;
    font-weight: 400;
    color: rgba(255, 255, 255, 0.2);
    margin-top: 1rem;
    transition: color .35s;
}
.more-btn-wrap{
    margin-top: 3rem;
    opacity: 0;
    visibility: hidden;
    transition: all .35s;
}
.more{
    position: relative;
    border: 1px solid #fff;
    font-size: 1.4rem;
    color: #fff;
    padding: 1rem 2rem;
    cursor: pointer;
    transition: all .35s;
}
.more:hover{
    background-color: #fff;
    color: #111;
}
.member-box:hover{
    background-color: rgba(1, 1, 1, 0.5);
}
.member-box:hover .member-txt,
.member-box:hover .member-title{
    color: #fff;
}
.member-box:hover .more-btn-wrap{
    opacity: 1;
    visibility: visible;
}