.allways-swiper{
  opacity:0
}
.allways-swiper a{
  display:block;
  height: 100%; 
}
.allways-img{
  position: relative;
  height: 42.9rem;
}
.allways-img .img-box{
  position: absolute;
  top:0;
  left:0;
  width:100%;
  height: 100%;
}
.always-info{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding:2rem;
  font-family: 'Inter';
  z-index: 1;
}
.always-info > span{
  display: flex;
  align-items: center;
  justify-content: center;
  width:5rem;
  height: 5rem;
  margin-bottom:1rem;
  background-color: #111;
  border-radius: 50%;
}
.always-info > span > em{ 
  color:#fff
}
.always-info > i{ 
  color:#A6A6A6
}
.allways-swiper .pagnation-box{
  position: relative;
  height: 0.3rem!important;
  margin: 2.5rem  0rem 0;
  background-color: rgba(0 0 0 / 0.05);
}
.allways-swiper .allways-swiper-pagination .swiper-pagination-progressbarl{
  height: 0.3rem;
}
.allways-swiper .allways-swiper-pagination .swiper-pagination-progressbar-fill{
  height: 0.3rem;
  background-color: #000;
}